<template>
	<div class="ljz">
		<p>前端性能优化 向下移动才会依次加载图片</p>
		<ul>
			<li v-for="item in imgs">
				<img :src="defaulturl" :data-src='item.imgurl'>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				imgs:[
					{
						imgurl:require('../../assets/images/0.jpg')
					},
					{
						imgurl:require('../../assets/images/1.jpg')
					},
					{
						imgurl:require('../../assets/images/2.jpg')
					},
					{
						imgurl:require('../../assets/images/3.jpg')
					},
					{
						imgurl:require('../../assets/images/4.jpg')
					}
				],
				defaulturl:require('../../assets/images/0.png')
			}
		},
		mounted(){

			let imgs = document.querySelectorAll('img');
			let canRun = true;

			window.onscroll = function() {
				if (!canRun) {
					return
				}
				canRun = false;
				setTimeout(() =>{
					lazyLoad();
					canRun = true;
				},1000)
			}

			function lazyLoad(){
				imgs.forEach((item) =>{
					if (item.offsetTop < window.innerHeight + window.scrollY - 100) {
						item.setAttribute('src',item.dataset.src)
					}
				})
			}

			lazyLoad()
		}
	}
</script>

<style lang="scss" scoped>
	.ljz{
		width: 100%;
		li{
			margin: 2rem 0;
			img{
				width: 100%;
			}
		}
	}
</style>